import React from "react";
import { useSelector } from "react-redux";
import { green } from "@material-ui/core/colors";
import {
  Box,
  Button,
  FormControlLabel,
  RadioGroup,
  Radio,
  Grid,
  FormControl
} from "@material-ui/core";

export default function Retirada(props) {
  const metodos = useSelector(state => state.payment.shippingMethods);
  const [value, setValue] = React.useState("");

  const handleChange = event => {
    setValue(event.target.value);
  };

  return (
    <Box p={2}>
      <h3>Local de Retirada</h3>
      <Grid container>
        <Grid item xs={12}>
          <Button onClick={props.prevClick}>Voltar</Button>
        </Grid>
        <Grid item xs={12}>
          <FormControl component="fieldset">
            <RadioGroup
              aria-label="retirada"
              name="retirada"
              value={value}
              onChange={handleChange}
            >
              {metodos.map(metodo => (
                <FormControlLabel
                  value={metodo.id}
                  control={<Radio />}
                  label={metodo.title + " - " + metodo.settings.cost.value}
                  key={metodo.id}
                />
              ))}
            </RadioGroup>
          </FormControl>
        </Grid>
        <Grid item xs={12} style={{ marginTop: 20 }}>
          <Button
            variant="contained"
            color="primary"
            style={{ backgroundColor: green[500], width: "100%" }}
            onClick={props.nextClick}
          >
            AVANÃ‡AR PARA O PAGAMENTO
          </Button>
        </Grid>
      </Grid>
    </Box>
  );
}
